.hidden {
  display: none;
  transition: all 0.3s ease
}
.show {
  display: inline;
  transition: all 0.3s ease
}

.logo {
  height: 4rem;
}
.main-photo-text {
  font-size: 5vh;
  color: #ffffff;
  text-shadow: 2px 2px 0 black, 2px -2px 0 black, -2px 2px 0 black,
    -2px -2px 0 black, 2px 0px 0 black, 0px 2px 0 black, -2px 0px 0 black,
    0px -2px 0 black;
}
.featured-photo {
  height: 15.5rem;
  min-width: 15rem;
  width: 100%;
}
.latest-artice-photo{
  height: 20rem;
}
.add-photo {
  height: 15.5rem;
}

.img-details {
  height: 31rem;
}
.small-feature-photo{
  height:10rem;
}

.fas {
  font-size: 1.5rem;
}
.fab {
  font-size: 1.5rem;
}


.front-page-footer {
  background-image: linear-gradient(white, rgb(180, 248, 245));
}
.footer-icon {
  font-size: 1.5rem;
}

.img-holder {
  position: relative;
  display: inline-block;
  height: 31rem;
}

.img-holder > div {
  position: absolute;
  z-index: 999;
  margin: 0 auto;
  left: 0;
  right: 0;
  top: 40%; /* Adjust this value to move the positioned div up and down */
  text-align: center;
  width: 60%; /* Set the width of the positioned div */
}

.travel-adjust {
  font-size: 1.5rem;
  background-color: Transparent;
    background-repeat:no-repeat;
    border: none;
    cursor:pointer;
    overflow: hidden;
    outline:none;
    color: rgb(0, 47, 255)
}

.travel-adjust:disabled,
button[disabled]{
  color: #666666;
}
.travelers-name{
  font-size: 0.85rem;
}
.travelers-min-max{
  font-size: 0.7rem;
}

.text-overlay{
  color: black
  
}

.bg-black{
  background-color: white;
}
.latest-paragraph{
  line-height: 1.8rem;
}

.contact-input {
  height: 50px;
}

.add-service-photo{
  height: 11rem;
  width: 11rem;
}
.title-img{
 height: 17rem;
}

.cart-menu{
  height: 100vh;
  width: 20rem;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 1070;
  overflow-y: scroll
}
.empty-cart-img-navbar{
  height: 6rem;
}
.navbar-cart-text{
  font-size: 12px;
}
.cart-travelers{
  height: 1.2rem;
}

.center{
  position: fixed;
  height:100%;
  width:100%;
  min-height: 100%;  /* Fallback for browsers do NOT support vh unit */
  min-height: 100vh; /* These two lines are counted as one :-)       */
background: transparent;
  align-items: center;
  z-index: 1070;
}

.cube{
  position: relative;
  z-index: 1090;
  height: 28rem;
  width: 38rem;
  top: 10%;
  overflow-y: scroll;
  background-color: rgb(180, 180, 180)
}
.fa-shopping-cart:hover{
cursor: pointer;
transform: scale(1.1)
}
.navbar-link{
  text-decoration: none;
  color: black;
}

.navbar-link:hover {
  text-decoration: none;
  color: black;
  transform: scale(1.1);
}

.dropdown:hover .dropdown-menu {
  display: block;
}
.navbar-toggler {
  border-color: rgb(100, 100, 100) !important;
}
.navbar-toggler-icon{
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(100,100,100, 1)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E") !important; 
}
.fa-times{
  position: absolute;
  top: 10px;
  right: 20px;
}
.fa-times:hover{
  cursor: pointer;
  transform: scale(1.1)
}

.cart-hidden{
  transform: translateX(100%);
  transition: all 0.3s ease
}
.cart-show{
  transform: translateX(0);
  transition: all 0.3s ease
}

@media (max-width: 767px) { 
  .img-holder {
    height: 18rem;
  }
  .cube{
    position: relative;
    z-index: 1070;
    height: 80vh;
    width: 300px;
    top: -10rem
  }
  .center{
    align-items: flex-start;
  }
 }

 @media (max-width: 576px) { 
  .img-holder {
    height: 14rem;
  }

 }